.footer {
  background-color: #00091C;
  padding: 50px 0px 40px 0px;
  color: rgba(255, 255, 255, 0.7490196078);
  font-size: 14px;
}
.footer .flexbox {
  display: flex;
}
.footer [class*=col_] {
  padding: 10px;
}
.footer .col_1 {
  width: 15.957%;
}
.footer .col_2 {
  width: 19%;
}
.footer .col_3 {
  width: 17.845%;
}
.footer .col_4 {
  width: 21.448%;
}
.footer .col_5 {
  width: 25.747%;
}
.footer .title {
  font-weight: 600;
  font-size: 16px;
  margin-bottom: 12px;
  color: #fff;
  line-height: 1.4;
}
.footer h4 {
  font-size: 14px;
  font-weight: 400;
}
.footer ul {
  margin-bottom: 30px;
}
.footer ul li {
  margin-bottom: 12px;
}
.footer .contact p {
  display: flex;
}
.footer .contact p i {
  width: 30px;
  flex-shrink: 0;
  font-size: 20px;
}
.footer .contact p:last-child {
  margin-bottom: 0;
}
.footer .line-bottom {
  border-color: #2E2E2E;
  margin-bottom: 30px;
}
.footer .logo {
  text-align: right;
}
.footer .logo img {
  max-width: 210px;
}
.footer .copyright p {
  margin-bottom: 0;
}
@media (max-width: 991px) {
  .footer {
    padding: 20px 0;
  }
  .footer .flexbox {
    flex-wrap: wrap;
  }
  .footer [class*=col_] {
    width: 33.33%;
  }
  .footer ul {
    margin-bottom: 15px;
  }
}
@media (max-width: 576px) {
  .footer [class*=col_] {
    width: 50%;
  }
  .footer .col_5 {
    width: 100%;
  }
  .footer .line-bottom {
    padding-bottom: 20px;
  }
  .footer .logo {
    text-align: left;
  }
  .footer .copyright {
    grid-template-columns: repeat(1, 1fr);
  }
}

.zalo-container {
  position: fixed;
  width: 40px;
  height: 40px;
  bottom: 210px;
  z-index: 9999999;
}
.zalo-container.right {
  transform: scale(1.6);
  right: 2.5rem;
}
.zalo-container a {
  display: block;
}
.zalo-container span {
  display: flex;
  align-items: center;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #1182FC;
  position: relative;
}
.zalo-container img {
  max-width: 100%;
  height: auto;
}
.zalo-container .cmoz-alo-circle {
  width: 50px;
  height: 50px;
  top: -5px;
  right: -5px;
  position: absolute;
  background-color: transparent;
  border-radius: 100%;
  border: 2px solid rgba(17, 130, 252, 0.8);
  opacity: 0.1;
  border-color: #1182FC;
  opacity: 0.5;
}
.zalo-container .cmoz-alo-circle-fill {
  width: 60px;
  height: 60px;
  top: -10px;
  position: absolute;
  transition: all 0.2s ease-in-out;
  border-radius: 100%;
  border: 2px solid transparent;
  transition: all 0.5s;
  background-color: rgba(17, 130, 252, 0.45);
  opacity: 0.75;
  right: -10px;
}
.zalo-container .animated_zalo {
  animation-duration: 1s;
  animation-fill-mode: both;
}
.zalo-container .animated_zalo.infinite {
  animation-iteration-count: infinite;
}
.zalo-container .zoomIn_zalo {
  animation-name: zoomIn_zalo;
}
.zalo-container .pulse_zalo {
  -webkit-animation-name: pulse_zalo;
  animation-name: pulse_zalo;
}

@keyframes zoomIn_zalo {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
  50% {
    opacity: 1;
  }
}
@-webkit-keyframes pulse_zalo {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }
  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}
@keyframes pulse_zalo {
  from {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
  50% {
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
  }
  to {
    -webkit-transform: scale3d(1, 1, 1);
    transform: scale3d(1, 1, 1);
  }
}